<!-- 已办详情 -->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />

      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">工单详情</p>
      </van-col>
    </van-row>
    <van-row class="dt-content">
      <van-row class="dt-table">
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">工单编号：</van-col>
          <span class="dt-item-content">{{order.SecunityCode}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">提交人：</van-col>
          <span class="dt-item-content">{{order.Creater}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">提交时间：</van-col>
          <span class="dt-item-content">{{order.CreateTime | filteDate}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-if="order.SecunityStatus == 1">
          <van-col :span="8"
                   class="dt-item-title">当前处理人：</van-col>
          <span class="dt-item-content">{{order.ChargeUserName}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-if="order.SecunityStatus == 2">
          <van-col :span="8"
                   class="dt-item-title">处理人：</van-col>
          <span class="dt-item-content">{{order.Modifier}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-if="order.SecunityStatus == 2">
          <van-col :span="8"
                   class="dt-item-title">处理时间：</van-col>
          <span class="dt-item-content">{{order.ModifyTime | filteDate}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">工单状态：</van-col>
          <span class="dt-item-content">{{order.SecunityStatus | filteOrderTStatus}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">工单类型：</van-col>
          <span class="dt-item-content">{{order.SecunityType | filteOrderType}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">工单内容：</van-col>
          <span class="dt-item-content">{{order.SecunityContent}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">订单编号：</van-col>
          <span class="dt-item-content">{{order.OrderNo}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">订单金额：</van-col>
          <span class="dt-item-content">{{order.OrderMoney}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">订单商品：</van-col>
          <span class="dt-item-content">{{order.OrderGoods}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">物流公司：</van-col>
          <span class="dt-item-content">{{order.LogisticsCompany}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title-pic">工单上报图片：</van-col>
        </van-row>
        <van-row gutter="10"
                 class="dt-pic">
          <van-uploader v-model="newImgList"
                        :deletable="false" />
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">完成说明：
          </van-col>
          <span class="dt-item-content">{{order.CahrgeContent}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title-pic">工单处理上报图片：</van-col>
        </van-row>
        <van-row gutter="10"
                 class="dt-pic">
          <van-uploader v-model="processImg"
                        :deletable="false" />
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">处理历史：</van-col>
        </van-row>
        <van-row class="dt-steps">
          <van-steps direction="vertical"
                     :active="0">
            <van-step v-for="item in this.historyList"
                      :key="item">
              <h3>{{item.content}}</h3>
              <p>{{item.date | filteDate}}</p>
            </van-step>
          </van-steps>
        </van-row>
        <van-row class="dt-ddd"></van-row>
      </van-row>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetPageContentByConditions, // 工单查询
} from "@/api/workOrder";
import { GetFileList } from "@/api/upFile";
import { getHistory } from "@/utils/order";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      order: {},
      processImg: [], // 处理图片
      newImgList: [], // 新增时上传的图片
      isStore: false, // 是否为商城工单
      historyList: [], // 处理历史列表
    };
  },
  filters: {
    filteDate (date) {
      if (date) {
        return date.replace("T", " ");
      }
    },
    filteOrderType (data) {
      let all = {
        1: '硬件工单',
        2: '突发工单',
        3: '保洁工单',
        4: '咨询工单',
        5: '投诉工单',
        6: '商城工单',
        7: '客流预警工单',
      }
      return all[data]
    },
    filteOrderTStatus (data) {
      let all = {
        1: '待处理',
        2: '已办理',
        3: '已指派',
      }
      return all[data]
    },
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.getworkOrderList()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 工单查询
    getworkOrderList () {
      GetPageContentByConditions({ ID: this.$route.query.ID }).then(response => {
        this.order = response.Data.ContentList[0]
        this.getImgList() // 获取图片
        // 获取处理历史
        let id = ''
        if (this.order.SecunityBussId == "0") {
          id = this.order.ID
        } else {
          id = this.order.SecunityBussId
        }
        getHistory(id, (data, CahrgeContent) => {
          this.order.CahrgeContent = CahrgeContent;
          if (data.length) {
            this.historyList = data
          } else {
            this.historyList.push({
              date: this.order.CreateTime,
              content: `${this.order.Creater}创建了工单`
            })
            this.historyList = [...this.historyList, ...data]
          }
        })
        // 判断是否为商城工单
        if (this.order.SecunityType == 6) {
          this.isStore = true
        } else {
          this.isStore = false
        }
      })
    },
    // 处理图片查询
    getImgList () {
      let info = {
        AssocID: this.order.ID, // 业务具体id
        PageIndex: 1,
        PageSize: 20
      }
      if (this.order.SecunityBussId !== "0") {
        info.AssocID = this.order.SecunityBussId // 业务具体id
      }
      GetFileList(info).then(response => {
        let imgList = response.Data.FileList
        imgList.forEach(item => {
          if (item.AssocSubModule == "完结") {
            this.processImg.push({ url: item.DownloadPath, isImage: true })
          } else if (item.AssocSubModule == "待处理") {
            this.newImgList.push({ url: item.DownloadPath, isImage: true })
          }
        })
      })
    },

    // 返回上一页按钮
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../../styles/dimens";
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: "SourceHanSansCN-Medium";
  color: #ecedf2;
}
.dt-content {
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
  overflow-y: auto;
  // background-image: url("../../../assets/img/背景图.png");
  background-image: url("../../../assets/img/工单背景.png");
  background-size: 100% 100%;
  // background-attachment: fixed;
  // background-position: 0 calc(#{$headerHeight} + #{$statusBarHeight});
  .dt-table {
    float: left; // 为了清除父子元素外边距重叠
    width: 94%;
    height: 86vh;
    margin: 0 3%;
    margin-top: px2rem(29);
    overflow-y: auto;
    .dt-item-title {
      width: px2rem(90);
      margin-left: px2rem(30);
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      color: #4c4c4c;
      font-weight: 500;
      line-height: px2rem(23);
      text-align: left;
    }
    .dt-item-title-pic {
      margin-left: px2rem(30);
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      color: #4c4c4c;
      font-weight: 500;
      line-height: px2rem(23);
      text-align: left;
    }
    .dt-item-content {
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      font-weight: 400;
      text-align: left;
      color: #4c4c4c;
      line-height: px2rem(23);
      width: px2rem(200);
      float: right;
      padding-right: px2rem(25);
    }
    .dt-pic {
      margin-top: px2rem(7.5);
      padding: 0 px2rem(30);
      overflow-y: auto;
      .dt-img {
        //图片
        position: relative;
        width: px2rem(89);
        height: px2rem(89);
        float: left;
        padding-top: 50%; /*padding-bottom 都可以*/
        .van-image {
          //图片
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .dt-top {
    margin-top: px2rem(7.5);
  }
  .dt-ddd {
    height: px2rem(100);
  }
}
.dt-steps {
  padding-left: px2rem(30);
  width: px2rem(300);
}
/deep/.van-uploader__upload {
  display: none;
}
/deep/.van-uploader {
  float: left;
}
</style>